html{
  overflow-x: hidden !important;
}

body{
  scroll-behavior: smooth !important;
  overflow-x: hidden !important;
}

.owl-dot{
  display: none !important; 
  @media (max-width:992px) {
    display: none !important;
  }
}

.owl-theme .owl-nav.disabled+.owl-dots {
  display: none !important; 
}